import theme from "../theme/theme";

interface Props {
  children: string;
}

function PageHeading({ children }: Props) {
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        width: "100%",
      }}
    >
      <h1>{children}</h1>
      <div
        style={{
          background: theme.palette.primary.main,
          width: "100px",
          height: "3px",
        }}
      ></div>
    </div>
  );
}

export default PageHeading;
